.product-designer-cta{
    position: relative;
    padding-top: 187px;
    padding-bottom: 88px;
    .pd-section-title{
        .text{
            padding: 24px 0;
            line-height: 1.4;
            @media (min-width:768px) {
                font-size: 20px;
                padding: 32px 0;
            }
        }
    }
    &__social-info{
        margin-bottom: 48px;
        h6{
            font-family: 'Work Sans';
            font-weight: 700;
            font-size: 24px;
            line-height: 32px;
            color: var(--bs-gray-v2-900);
            padding-bottom: 18px;
        }
        .social-media-button-group{
            padding: 0;
            margin: 0;
            list-style: none;
            display: grid;
            gap: 18px;
            grid-template-columns: repeat(3, minmax(140px, 200px));
        }
    }
    &__contact-info{
        h6{
            font-family: 'Work Sans';
            font-weight: 700;
            font-size: 24px;
            line-height: 32px;
            color: var(--bs-gray-v2-900);
            padding-bottom: 18px;
        }
    }
    &__wave{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }
}
.pd-social-btn{
    display: inline-block;
    background: var(--bs-gray-v2-50);
    width: 204px;
    padding: 24px;
        font-family: 'Work Sans', sans-serif;
        font-weight: 500;
        font-size: 18px;
        line-height: 24px;
        color: var(--bs-gray-v2-900);
    svg{
        margin-right: 12px;
    }
}
.pd-contact-info-card{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 16px;
    padding: 32px;
    display: flex;
    gap: 32px;
    justify-content: space-between;
    @media (max-width:575px) {
        flex-direction: column;
    }
    .content{
        display: flex;
        flex-direction: column;
        text-align: center;
        @media (min-width:575px) {
            width: 276px;
        }
        svg{
            margin-bottom: 16px;
            margin: auto;
        }
        span{
            font-family: 'Work Sans', sans-serif;
            padding-bottom: 3px;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: rgb(255, 255, 255,0.7);
        }
        p{
            font-family: 'Work Sans', sans-serif;
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            color: white;
        }
    }
}